// =============================================
// Compass Defaults
// =============================================

// Border Radius

$default-border-radius: 5px;

// Transition

$default-transition-property: all;
$default-transition-duration: 200ms;
$default-transition-function: linear;
$default-transition-delay: 0;

// =============================================
// Primary Break Points
// =============================================

// These should be used with the bp (max-width, xx) mixin
// where a min-width is used, remember to +1 to break correctly.
// If these are changed, they must also be updated in app.js

$bp-portrait: 360px;
$bp-xxs: 440px;
$bp-xsmall: 480px;
$bp-small: 676px;
$bp-medium: 770px;
$bp-large: 960px;
$bp-xlarge: 1199px;
$bp-xxl: 1366px;
$bp-xxxl: 1600px;

// =============================================
// Base Values
// =============================================

// Spacing

$gap: 10px;

$trim: 30px;
// This will get applied on viewports smaller than 480px
$trim-small: 15px;

// Padding that will get applied to content areas
$box-spacing-large: 20px;

// Standard padding around box elements such as banner messages, etc
$box-spacing: 10px;

// Standard spacing between elements within a box, such as "Add to cart", "Price box", etc
$element-spacing: 7px;

// Dimensions

$max-content-width: 1200px;
$max-container-width: 1336px;
$max-std-formatted-width: 50em;

// =============================================
// Brand Colors
// =============================================

// Usually not used directly in Sass partials.
// Only used to define context-based color vars in this file.

$c-black: #000000;
$c-blue: #3399CC;
$c-light-gray: #EEE;
$c-gray: #CCC;
$c-green: #11B400;
$c-orange: #F3793B;
$c-pink: #D85378;
$c-purple: #b8b4d9;
$c-purple2: #a699c7;
$c-red: #CF5050;
$c-yellow: #FFDD15;
$c-white: #FFFFFF;

// =============================================
// Base Colors
// =============================================

// Text

$c-text: #636363;
$c-footer-text: #b4b5ba;
$c-text-light-gray: #DDD;
$c-text-gray: #A0A0A0;
$c-text-white: #FFF;
$c-text-light-purple: #d9d8e6;
$c-text-purple: #6c5b9f;
// Primary font color for headings and other non-link text
$c-text-primary: $c-text-purple;


// Interaction

$c-action: $c-purple;
$c-stimulus: darken($c-text-purple, 15%);
$c-subtle: #A0A0A0;

// Notifications

$c-danger: #DF280A;
$c-danger-background: #FAEBE7;

$c-success: $c-green;
$c-success-background: #EFF5EA;

$c-warn: #E26703;
$c-warn-background: #F9EBE6;

$c-dark: #676157;

// Global Elements

$c-divider-border: #ECECEC;

// =============================================
// Element Colors
// =============================================

// Buttons

$c-button: $c-purple;
$c-button-hover: darken($c-purple, 5%);
$c-button-active: darken($c-purple, 10%);

// Secondary Buttons

$c-button-secondary: $c-purple;
$c-button-secondary-hover: darken($c-purple, 5%);
$c-button-secondary-active: darken($c-purple, 10%);

$c-button-subtle: $c-purple;

// Forms

$c-input-border: #C0C0C0;
$c-input-border-focus: $c-purple;

$c-input-placeholder: $c-text-gray;
$c-input-text: $c-text;

$c-fieldset-border: #CCCCCC;
$c-fieldset-border-light: #EDEDED;

// Headings

$c-h1: $c-text-purple;
$c-h2: $c-text;
$c-h3: $c-text;
$c-h4: $c-text;
$c-h5: $c-text;
$c-h6: $c-text;

// Link
$c-link: $c-action;
$c-link-hover: darken($c-action, 5%);
$c-link-focus: $c-action;
$c-link-active: darken($c-action, (5% / 2));

// Module (generic)

$c-module-background: #F4F4F4;
$c-module-background-hover: shade($c-module-background, 3%);
$c-module-background-secondary: #EAF8FF;
$c-module-background-dark: #AAAAAA;
// This should be applied whenever the border is surrounding a white element and/or is directly showing on white.
$c-module-border: #CCCCCC;
// This should be applied whenever the border is wrapping an element with $c-module-background
$c-module-border-light: #EDEDED;
$c-module-border-dark: #777777;
$c-module-border-highlight: $c-text-purple;


// Table

$c-table-background: #F4F4F4;
$c-table-border: #C0C0C0;

$c-table-zebra-odd: #F8F7F5;
$c-table-zebra-even: #EEEDED;

// Tabs

$c-tabs-background: #F0F0F0;

// Email
$c-email-background: #EBEBEB;
$c-email-background-secondary: #E1F0F8;
$c-email-border: #C3CED4;
$c-email-action: #3696C2;
$c-email-cell: #F1F1F1;
$c-email-highlighted-background: #FFFDD9;
$c-email-highlighted-border: #FFF74C;

// =============================================
// Typography
// =============================================

// Google Web Font stylesheet is included via layout XML

// Font Stacks

//$f-stack-sans: 'Open Sans', Verdana, Arial, sans-serif;
$f-stack-sans: sans-serif;
$f-stack-serif: Georgia, Times, 'Times New Roman', serif;
$f-stack-special: 'Open sans', 'Helvetica Neue', Verdana, Arial, sans-serif;

$f-stack-default: $f-stack-sans;

// Font Sizes

$f-size-xxxl: 22px;
$f-size-xxl: 18px;
$f-size-xl: 16px;
$f-size-l: 15px;

$f-size: 14px;

$f-size-s: 13px;
$f-size-xs: 12px;
$f-size-xxs: 11px;

// Base Measures

$b-line-height: 1.5;
$b-margin-bottom: 1.5em;
